<template>
  <div class="search-form">
    <form class="form-inline" action="">
      <div class="row">

        <!--Departure-->
        <div class="col-xs-12 col-sm-6 col-md-4 col-lg-4 item-form">
          <div>
            <label>T(sn bay)</label>
            <address-auto-complete
              id="departure"
              class="address-auto-complete"
              :types="'departure'">
            </address-auto-complete>
          </div>
        </div>
        <!--End Departure-->

        <!--Arrival-->
        <div class="col-xs-12 col-sm-6 col-md-4 col-lg-4 item-form">
          <div>
            <label>n(a ch)</label>
            <address-auto-complete
              id="arrival"
              class="address-auto-complete"
              :types="'arrival'">
            </address-auto-complete>
          </div>
        </div>
        <!--End Arrival-->

        <!--Date-->
        <div class="col-xs-6 col-sm-6 col-md-2 col-lg-2 item-form">
          <div>
            <label>Ngy n</label>
            <el-date-picker v-model="datePicker" :picker-options="datePickerOption"
                            format="dd/MM/yyyy"
                            style="width: 100%" type="date">
            </el-date-picker>
          </div>
        </div>
        <!--End date-->

        <!--Time-->
        <div class="col-xs-6 col-sm-6 col-md-2 col-lg-2 item-form">
          <div>
            <label>Gi n</label>
            <el-time-select v-model="timePick" :picker-options="timePickerOption"
                            format="HH:mm"
                            style="width: 100%">
            </el-time-select>
          </div>
        </div>
        <!--End time-->
      </div>

      <div class="action text-right" style="margin-top: 20px">
        <el-button class="search" type="primary" :loading="objSearch.isLoading" v-on:click="search()">
          {{objSearch.textSearch}}
        </el-button>
      </div>
    </form>
  </div>

</template>

<script>
  import AddressAutoComplete from './AddressAutoComplete'

  export default {
    name: "SearchForm",
    components: {AddressAutoComplete},
    data() {
      return {
        address: '',
        timePick: "06:00",
        timePickerOption: {
          start: '00:00',
          step: '00:15',
          end: '23:45'
        },
        datePicker: Date.now() + 3600 * 1000 * 24,
        datePickerOption: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 3600 * 1000 * 24;
          },
          tomorrow: Date.now()
          ,
          shortcuts: [{
            text: 'Hm nay',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          },
            {
              text: 'Ngy mai',
              onClick(picker) {
                const date = new Date();
                date.setTime(date.getTime() + 3600 * 1000 * 24);
                picker.$emit('pick', date);
              }
            },
            {
              text: 'Ngy kia',
              onClick(picker) {
                const date = new Date();
                date.setTime(date.getTime() + 3600 * 1000 * 24 * 2);
                picker.$emit('pick', date);
              }
            }]
        },
        objSearch: {
          isLoading: false,
          textSearch: 'Tm xe'
        },
      }
    },

    methods: {
      search() {
          this.objSearch.isLoading = true,
          this.objSearch.textSearch = 'ang tm...'
      },

      onTransportTabClick(tab, event) {
        console.log(tab, event);
      }
    },
  }
</script>

<style scoped>
  @import "../../assets/css/booking/search_form.css";
</style>



