<template>
  <div class=" login">
    <div class="login_head_bg">

    </div>

    <div class="login_page">
      <div v-if="!isSubmit">
        <div class="login_head_submit_wrap">
          <div class="row">
            <div class="col-xs-6">
              <div class="login_head_logo_link text-left">
                <a style="color: #ffffff" href="#">
                  <b> <img style="width: 18px" src="../../assets/images/left-arrow.svg"
                           alt="Tip theo">
                    Tr v</b></a>
              </div>

            </div>
            <div class="col-xs-6">
              <div class="text-right login_head_submit_btn">
                <b v-on:click="next()" style=" cursor: pointer"> Tip theo <img
                  style="width: 18px; cursor: pointer"
                  src="../../assets/images/right-arrow.svg"
                  alt="Tip theo"></b>
              </div>
            </div>
          </div>
        </div>
        <div class="login_form_wrap">
          <div>
            <p style="color: #000000"><b>ng nhp</b></p>
            <p style="color: #708198; font-size: 14px; font-style: italic">Vui lng nhp chnh xc s in
              thoi</p>
          </div>
          <el-form ref="form">
            <!--S in thoi v emain-->
            <el-form-item>
              <el-col :span="24">
                <el-form-item prop="cellphone">
                  <el-form-item>
                    <el-input v-model="cellphone"
                              placeholder="Nhp s in thoi ti y!"
                              style="padding-top: 20px"></el-input>
                  </el-form-item>
                </el-form-item>
              </el-col>

            </el-form-item>     <!--END S in thoi v emain-->
          </el-form>
        </div>
      </div>
      <div class="confirm-code text-center login_form_wrap" v-if="isSubmit">
        <h4 style="margin-bottom: 0px; font-weight: bold">+84 988996989</h4>
        <a href="#" style="color: #0084ff; font-size: 14px" v-on:click="isSubmit = !isSubmit">Sa s in thoi</a>
        <p style="padding-top: 20px; color: #708198">Chng ti  gi m code n s in thoi ny. Vui lng
          nhp m code vo y!</p>
        <el-form ref="confirm">
          <el-form-item>
            <el-col :span="24">
              <el-form-item prop="cellphone">
                <el-form-item label="M code gm 6 k t">
                  <el-input v-model="code"
                            placeholder="# # # # # #"
                  ></el-input>
                </el-form-item>
              </el-form-item>
            </el-col>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>

  import {Form, FormItem, Col, MessageBox, Input} from 'element-ui'
  import Vue from 'vue'

  Vue.use(Form)
  Vue.use(FormItem)
  Vue.use(Col)
  Vue.use(Input)
  Vue.prototype.$confirm = MessageBox.confirm;

  export default {
    name: "login",
    data() {
      return {
        isSubmit: false,
        labelPosition: 'top',
        cellphone: null,
        code: null
      }
    },
    methods: {
      next() {
        this.$confirm('<div class="text-center"><p class="text-center" style="margin-bottom: 0px"><b>' + this.cellphone + '</b></p>Bn chc l s in thoi ca bn. Tip tc?</div>', 'Xc nhn', {
          dangerouslyUseHTMLString: true,
          confirmButtonText: 'Chc chn',
          cancelButtonText: 'Hy',
          type: 'confirm'
        }).then(() => {
          // this.$message({
          //     type: 'success',
          //     message: 'Delete completed',
          //
          // });
          this.isSubmit = true;
        }).catch(() => {
          this.$message({
            type: 'info',
            message: 'Delete canceled'
          });
        });
      }
    }
  }
</script>

<style scoped>
  @import "../../assets/css/login/login.css";
</style>



