        <div class="article-container ">
            <div><h4><strong>B QUY TC NG X   QUY NH CA LI XE</strong></h4></div>
            <div class="content text-justify">
                <p>8Go duy tr&igrave; b quy tc ng x d&agrave;nh cho tt c i t&aacute;c t&agrave;i x  h c&oacute; mt hiu bit r&otilde; r&agrave;ng v vic tu&acirc;n th ph&aacute;p lut, an to&agrave;n, an ninh v&agrave; ti&ecirc;u chun dch v h cn c&oacute;. V&igrave; 8Go cam kt mt ch&iacute;nh s&aacute;ch kh&ocirc;ng tha hip vi nhng h&agrave;nh vi vi phm, nhng h&agrave;nh vi n&agrave;y c&oacute; th dn ti vic t&agrave;i khon ca t&agrave;i x b chn tm thi hoc vnh vin khi ng dng 8Go.</p>
                <p>Mi t&agrave;i x 8Go s phi tu&acirc;n theo b quy tc ng x sau &acirc;y:</p>
                <h4><strong>1.&nbsp;&nbsp;TU&Acirc;N TH LUT, QUY NH, CH&Iacute;NH S&Aacute;CH V&Agrave; C&Aacute;C HNG DN CA 8GO.</strong></h4>
                <ol>
                    <li>T&agrave;i x phi tu&acirc;n th lut giao th&ocirc;ng, quy nh, quy tc, ch&iacute;nh s&aacute;ch, hng dn v&agrave; b quy tc ng x n&agrave;y cng nh bt k ni quy n&agrave;o ph&aacute;t sinh trong qu&aacute; tr&igrave;nh hot ng.</li>
                    <li>T&agrave;i x cam kt kh&ocirc;ng tham gia bt k hot ng n&agrave;o l&agrave;m nh hng hoc tn hi n danh ting v&agrave;/hoc l&agrave;m gi&aacute;n on hot ng ca 8Go, ng thi kh&ocirc;ng cu kt vi c&aacute;c t&agrave;i x kh&aacute;c hoc b&ecirc;n th ba nhm mc &iacute;ch thc hin c&aacute;c h&agrave;nh vi n&ecirc;u tr&ecirc;n.</li>
                    <li>8Go ang c gng ht sc  mang n nhiu cuc xe hn cho t&agrave;i x. V&igrave; vy, ch&uacute;ng t&ocirc;i cn s h tr ca t&agrave;i x i vi nhng cuc xe d&ugrave;ng m&atilde; khuyn m&atilde;i. H&atilde;y tr&acirc;n trng tt c c&aacute;c cuc xe n&agrave;y khi nhn c x&aacute;c nhn t kh&aacute;ch h&agrave;ng. Trong trng hp t&agrave;i x kh&ocirc;ng chn chn, h&atilde;y li&ecirc;n h vi b phn chm s&oacute;c kh&aacute;ch h&agrave;ng  c gii &aacute;p thc mc.</li>
                    <li>T&agrave;i x b nghi&ecirc;m cm thc hin nhng h&agrave;nh ng nhm mc &iacute;ch gian ln hoc &aacute;nh la, g&acirc;y ra tht tho&aacute;t cho 8Go di bt k h&igrave;nh thc n&agrave;o, kh&ocirc;ng ch ri&ecirc;ng M&atilde; khuyn m&atilde;i.</li>
                    <li>8Go ngn chn c&aacute;c t&agrave;i x tham gia v&agrave;o bt k cuc biu t&igrave;nh phi ph&aacute;p n&agrave;o chng li 8Go, l&agrave;m th&agrave;nh vi&ecirc;n ca c&aacute;c hip hi kh&ocirc;ng c&oacute; giy ph&eacute;p, x&uacute;i gic c&aacute;c t&agrave;i x kh&aacute;c ngng hot ng, ty chay hoc e da ty chay ng dng 8Go hoc bt k h&agrave;nh ng n&agrave;o i ngc li li &iacute;ch ca 8Go.</li>
                    <li>T&agrave;i x mang n nhng chuyn i an to&agrave;n cho tt c h&agrave;nh kh&aacute;ch. S an to&agrave;n ca h&agrave;nh kh&aacute;ch v&agrave; t&agrave;i x khi s dng ng dng 8Go  di chuyn l&agrave; mi quan t&acirc;m h&agrave;ng u ca ch&uacute;ng t&ocirc;i.</li>
                    <li>8Go kh&ocirc;ng tha hip vi bt k h&agrave;nh vi s dng ma t&uacute;y hoc ru bia n&agrave;o khi tham gia hot ng qua ng dng 8Go. iu n&agrave;y tr&aacute;i vi ph&aacute;p lut, ng thi vi phm nghi&ecirc;m trng ch&iacute;nh s&aacute;ch an to&agrave;n ca 8Go. Hn na, 8Go nghi&ecirc;m cm vic t&agrave;ng tr v&agrave; ct gi cht cm cng nh ru bia trong xe ca t&agrave;i x. Nu mt h&agrave;nh kh&aacute;ch nghi ng t&agrave;i x c&oacute; s dng ma t&uacute;y hoc ru bia, h&agrave;nh kh&aacute;ch &oacute; c&oacute; quyn kt th&uacute;c chuyn i ngay lp tc v&agrave; phn &aacute;nh li s vic vi b phn chm s&oacute;c kh&aacute;ch h&agrave;ng.</li>
                    <li>T&agrave;i x b nghi&ecirc;m cm s dng hoc ct gi bt k loi v kh&iacute; n&agrave;o trong xe hoc tr&ecirc;n ngi vi bt k mc &iacute;ch g&igrave;.</li>
                    <li>8Go lu&ocirc;n nghi&ecirc;m khc vi nhng h&agrave;nh vi l&aacute;i xe kh&ocirc;ng an to&agrave;n, g&acirc;y nguy him cho h&agrave;nh kh&aacute;ch v&agrave; nhng ngi i ng kh&aacute;c, bao gm:</li>
                </ol>
                <h4><strong>2. DCH V C&Oacute; CHT LNG V&Agrave; CHUY&Ecirc;N NGHIP</strong></h4>
                <ul>
                    <li>L&aacute;i xe lng l&aacute;ch, &aacute;nh v&otilde;ng</li>
                    <li>Vt qu&aacute; tc  cho ph&eacute;p</li>
                    <li>Kh&ocirc;ng tu&acirc;n th t&iacute;n hiu &egrave;n giao th&ocirc;ng</li>
                    <li>S dng in thoi di ng bng tay l&uacute;c l&aacute;i xe</li>
                    <li>Kh&ocirc;ng bt &egrave;n xi nhan khi sang l&agrave;n kh&aacute;c hoc r hng kh&aacute;c</li>
                    <li>Vi phm bt k lut giao th&ocirc;ng n&agrave;o kh&aacute;c</li>
                </ul>
                <ol>
                    <li>T&agrave;i x c&oacute; tr&aacute;ch nhim bo qun xe trong iu kin tt, &uacute;ng vi ti&ecirc;u chun an to&agrave;n ca ng&agrave;nh giao th&ocirc;ng. T&agrave;i x cng n&ecirc;n kim tra sc khe theo quy nh ca a phng hoc khi c 8Go hng dn.</li>
                    <li>T&agrave;i x lu&ocirc;n n mc ph&ugrave; hp v&agrave; chn chu, ng thi cho thy t&aacute;c phong chuy&ecirc;n nghip cng nh giao tip lch s vi h&agrave;nh kh&aacute;ch.</li>
                    <li>T&agrave;i x kh&ocirc;ng c ph&eacute;p l&aacute;i xe nu kh&ocirc;ng c&oacute; bng l&aacute;i xe hp ph&aacute;p, ch  bo him v&agrave;/hoc giy ph&eacute;p kinh doanh trong trng hp cho thu&ecirc; xe theo hp ng.</li>
                    <li>8Go khuyn kh&iacute;ch t&agrave;i x lu&ocirc;n nhc nh h&agrave;nh kh&aacute;ch c&agrave;i d&acirc;y an to&agrave;n trong l&uacute;c di chuyn vi 8Go.</li>
                    <li>Tr&aacute;nh bo lc v&agrave; th&aacute;i  c&aacute;u gt vi h&agrave;nh kh&aacute;ch bt c l&uacute;c n&agrave;o. Vi ti&ecirc;u ch&iacute; an to&agrave;n v&agrave; an ninh l&agrave; tr&ecirc;n ht, 8Go kh&ocirc;ng chp nhn bt k h&agrave;nh ng bo lc hoc th&aacute;i  ti&ecirc;u cc n&agrave;o. T&agrave;i x kh&ocirc;ng c ph&eacute;p th hin th&aacute;i  c&aacute;u gt, bao gm tranh chp vi h&agrave;nh kh&aacute;ch, chi tc, mit th, e da hoc x&ocirc; x&aacute;t vi h&agrave;nh kh&aacute;ch. Trong trng hp bt kh kh&aacute;ng, t&agrave;i x kh&ocirc;ng n&ecirc;n t x l&yacute; hoc l&agrave;m tng cng thng (v&iacute; d: c&ocirc;ng k&iacute;ch hoc e da d&ugrave; ang trong chuyn i hay &atilde; kt th&uacute;c). H&atilde;y b&aacute;o c&aacute;o t&igrave;nh h&igrave;nh vi 8Go c&agrave;ng sm c&agrave;ng tt. Ch&uacute;ng t&ocirc;i hng n gii quyt vn  ca c&aacute;c b&ecirc;n li&ecirc;n quan mt c&aacute;ch c&ocirc;ng bng v&agrave; h&agrave;nh ng hp t&igrave;nh hp l&yacute; nht.</li>
                    <li>Nghi&ecirc;m cm bt k h&agrave;nh vi x&acirc;m phm t&igrave;nh dc, bao gm: b&igrave;nh lun/tin nhn d&acirc;m dc, quy ri t&igrave;nh dc, ng chm th&acirc;n th kh&ocirc;ng mong mun, khng ch tr&aacute;i lut, chuc thuc, cng bc v&agrave; h&atilde;m hip. &acirc;y l&agrave; nhng h&agrave;nh vi phm ph&aacute;p v&agrave; vi phm nghi&ecirc;m trng ch&iacute;nh s&aacute;ch an to&agrave;n ca 8Go. Ngi vi phm s b truy t tr&aacute;ch nhim h&igrave;nh s.</li>
                    <li>H&atilde;y ghi nh rng quy ri mang &yacute; ngha kh&aacute;c nhau t&ugrave;y theo quy chun ca x&atilde; hi v&agrave; c&aacute; nh&acirc;n &ndash; &aacute;nh gi&aacute; ngoi h&igrave;nh, hi qu&aacute; nhiu c&acirc;u hi c&aacute; nh&acirc;n v&agrave; x&acirc;m phm kh&ocirc;ng gian ri&ecirc;ng thng c xem l&agrave; kh&ocirc;ng ph&ugrave; hp. 8Go khuyn c&aacute;o t&agrave;i x n&ecirc;n lu &yacute; n t do v&agrave; kh&ocirc;ng gian c&aacute; nh&acirc;n ca h&agrave;nh kh&aacute;ch. Nhn tin cho h&agrave;nh kh&aacute;ch ngo&agrave;i mc &iacute;ch c&ocirc;ng vic va l&agrave; mt h&igrave;nh thc quy ri va l&agrave; mt h&agrave;nh ng vi phm quyn an to&agrave;n v&agrave; bo mt th&ocirc;ng tin c&aacute; nh&acirc;n.</li>
                    <li>L&agrave; mt ngi cung cp dch v di chuyn, 8Go khuyn kh&iacute;ch t&agrave;i x gi&uacute;p  nhng h&agrave;nh kh&aacute;ch khuyt tt, c bit khi l&ecirc;n hoc xung xe.</li>
                    <li>T&agrave;i x phi &aacute;p ng c&aacute;c y&ecirc;u cu hp l&yacute; ca h&agrave;nh kh&aacute;ch nh chnh nhit  m&aacute;y iu h&ograve;a hoc &acirc;m lng radio.</li>
                    <li>Vi s quan t&acirc;m d&agrave;nh cho c&aacute;c h&agrave;nh kh&aacute;ch khim th c&oacute; ch&oacute; dn ng i theo, t&agrave;i x c khuyn kh&iacute;ch nhn c&aacute;c cuc xe &oacute; nu kh&ocirc;ng tr&aacute;i vi c&aacute;c gi&aacute;o l&yacute; trong t&ocirc;n gi&aacute;o ca t&agrave;i x.</li>
                    <li>T&agrave;i x c khuyn kh&iacute;ch nm r&otilde; c&aacute;c din bin tr&ecirc;n ng. T&agrave;i x s hot ng thun li hn khi nm c c&aacute;c iu kin giao th&ocirc;ng nh c&ocirc;ng trng n&agrave;o ang thi c&ocirc;ng, c&oacute; c&aacute;c v tai nn ln hoc c&aacute;c s kin ngo&agrave;i tri hay kh&ocirc;ng, nhm tr&aacute;nh nh hng n l tr&igrave;nh.</li>
                    <li>Trong trng hp h&agrave;nh kh&aacute;ch  qu&ecirc;n t&agrave;i sn tr&ecirc;n xe, t&agrave;i x c&oacute; tr&aacute;ch nhim tr li cho h&agrave;nh kh&aacute;ch &oacute; ngay lp tc hoc nh b phn chm s&oacute;c kh&aacute;ch h&agrave;ng ca 8Go gi li.</li>
                    <li>Tr&acirc;n trng cuc xe m&agrave; t&agrave;i x nhn c. iu n&agrave;y v&ocirc; c&ugrave;ng quan trng trong vic x&acirc;y dng s tin tng ca kh&aacute;ch h&agrave;ng v&agrave; khuyn kh&iacute;ch nhiu h&agrave;nh kh&aacute;ch s dng dch v G8o hn na. Nhiu h&agrave;nh kh&aacute;ch hn c&oacute; ngha l&agrave; nhiu cuc xe hn cho to&agrave;n th t&agrave;i x.</li>
                    <li>Kh&ocirc;ng chuyn kh&aacute;ch cho t&agrave;i x kh&aacute;c. Trong trng hp t&agrave;i x kh&ocirc;ng th &oacute;n kh&aacute;ch v&igrave; c&oacute; vic t xut, vui l&ograve;ng gi ngay cho h&agrave;nh kh&aacute;ch  hy t xe. T&agrave;i x c khuyn kh&iacute;ch kh&ocirc;ng hy cuc xe, tr trng hp khn cp (v&iacute; d xe h).</li>
                    <li>Ch kh&aacute;ch h&agrave;ng n im &oacute;n. Nu t&agrave;i x b i m&agrave; kh&ocirc;ng ch h, h s c&oacute; tri nghim ti&ecirc;u cc i vi dch v ca 8Go. Vui l&ograve;ng gi cho h&agrave;nh kh&aacute;ch sau khi i 5 ph&uacute;t m&agrave; h kh&ocirc;ng n  x&aacute;c nhn xem h&agrave;nh kh&aacute;ch c&oacute; mun tip tc i xe hay kh&ocirc;ng. H&atilde;y b&aacute;o cho h&agrave;nh kh&aacute;ch v&agrave; b phn chm s&oacute;c kh&aacute;ch h&agrave;ng nu t&agrave;i x kh&ocirc;ng th tip tc i.</li>
                    <li>Kh&ocirc;ng nhn cuc xe khi tr&ecirc;n xe &atilde; c&oacute; kh&aacute;ch v&igrave; iu n&agrave;y s khin kh&aacute;ch h&agrave;ng mi phi i l&acirc;u hn.</li>
                    <li>T&agrave;i x kh&ocirc;ng c ph&eacute;p t chi cuc xe da tr&ecirc;n nhng c im c&aacute; nh&acirc;n ca kh&aacute;ch nh chng tc, t&ocirc;n gi&aacute;o, quc tch, khuyt tt, xu hng t&igrave;nh dc, gii t&iacute;nh, t&igrave;nh trng h&ocirc;n nh&acirc;n, c im gii t&iacute;nh, tui t&aacute;c hay bt k c t&iacute;nh n&agrave;o c bo v bi ph&aacute;p lut. H&agrave;nh ng n&agrave;y c&oacute; th dn n vic t&agrave;i x b chn vnh vin khi ng dng 8Go. iu n&agrave;y th hin s thiu t&ocirc;n trng, g&acirc;y n&ecirc;n s k&igrave; th v&agrave; nh hng n quyn li ca mt c&aacute; nh&acirc;n hoc mt nh&oacute;m ngi n&agrave;o &oacute;, kh&ocirc;ng cn bit h&agrave;nh kh&aacute;ch c&oacute; thuc nh&oacute;m ngi &oacute; hay kh&ocirc;ng.</li>
                    <li>Kh&ocirc;ng t &yacute; a ra mc ph&iacute; (v&iacute; d ph thu mi chuyn i) bi &oacute; l&agrave; h&agrave;nh vi tng gi&aacute; tr&aacute;i qui nh. Ch nhn &uacute;ng s tin cc da tr&ecirc;n ng h c&ugrave;ng vi c&aacute;c khon thu a ra bi c&aacute;c c&ocirc;ng ty uy t&iacute;n hoc da tr&ecirc;n ng dng.</li>
                    <li>Kh&ocirc;ng gi &yacute; kh&aacute;ch i ri&ecirc;ng hoc sp xp t th&ecirc;m chuyn. iu n&agrave;y vi phm quy nh ca 8Go v vic ch nh xe gn nht cho h&agrave;nh kh&aacute;ch. T&agrave;i x cng ang ngn h&agrave;nh kh&aacute;ch t xe mt c&aacute;ch hiu qu nht.</li>
                    <li>Kh&ocirc;ng gii thiu c&aacute;c ng dng t xe kh&aacute;c cho h&agrave;nh kh&aacute;ch. iu n&agrave;y g&acirc;y ra s kh&oacute; chu v&agrave; to cm gi&aacute;c thiu chuy&ecirc;n nghip cho h&agrave;nh kh&aacute;ch.</li>
                    <li>H&agrave;nh kh&aacute;ch mong mun s th&agrave;nh tht  mt ngi t&agrave;i x. V&igrave; vy, t&agrave;i x phi cung cp dch v di chuyn da tr&ecirc;n quy tc n&agrave;y, v&agrave; nhng h&agrave;nh vi gian ln nh kh&ocirc;ng a tin tha hoc a &iacute;t tin tha hn cho h&agrave;nh kh&aacute;ch, hoc i ng v&ograve;ng xa hn cn thit (tr trng hp c&oacute; l&yacute; do ch&iacute;nh &aacute;ng hay do h&agrave;nh kh&aacute;ch y&ecirc;u cu) u b ngn cm.</li>
                    <li>T&agrave;i x b nghi&ecirc;m cm li&ecirc;n lc vi h&agrave;nh kh&aacute;ch qua bt k k&ecirc;nh li&ecirc;n lc hay ng dng n&agrave;o kh&aacute;c ngo&agrave;i mc &iacute;ch c&ocirc;ng vic.</li>
                    <li>Nhn &ldquo;Tr kh&aacute;ch&rdquo;/ &ldquo;Kt th&uacute;c chuyn i&rdquo; ch sau khi h&agrave;nh kh&aacute;ch xung xe.</li>
                    <li>Nhanh ch&oacute;ng b&aacute;o c&aacute;o th&ocirc;ng tin khn cp n 8Go. Ch&uacute;ng t&ocirc;i mong mun gi&uacute;p  trong mi vn  ph&aacute;t sinh.</li>
                    <li>Bt c l&uacute;c n&agrave;o t&agrave;i x thy ang i din vi t&igrave;nh hung cn s gi&uacute;p  khn cp, vui l&ograve;ng gi s dch v khn cp ti khu vc. Khi tt c c&aacute;c b&ecirc;n li&ecirc;n quan &atilde; an to&agrave;n v&agrave; t&igrave;nh hung c gii quyt bi lc lng chc nng, h&atilde;y th&ocirc;ng b&aacute;o cho G8o. Ch&uacute;ng t&ocirc;i s h tr v&agrave; c&oacute; ng th&aacute;i ph&ugrave; hp nu cn thit, bao gm vic hp t&aacute;c vi c&aacute;c c quan ph&aacute;p lut.</li>
                    <li>Tip thu nhng g&oacute;p &yacute; v&agrave; tham gia c&aacute;c kh&oacute;a &agrave;o to. Ti 8Go, ch&uacute;ng t&ocirc;i cam kt kh&ocirc;ng ngng ci thin cht lng dch v ca m&igrave;nh.</li>
                    <li>8Go trin khai h thng nhn phn hi 2 chiu t t&agrave;i x v&agrave; h&agrave;nh kh&aacute;ch, cng nh h thng kim tra cht lng ca c&aacute;c h&agrave;nh kh&aacute;ch b&iacute; mt. Ch&uacute;ng t&ocirc;i cng thc hin h thng &aacute;nh gi&aacute; mc  ph&ugrave; hp v&agrave; biu hin ca t&agrave;i x li&ecirc;n tc, sau &oacute; a ra c&aacute;c phn hi t&ugrave;y theo tng trng hp. a s trng hp, ch&uacute;ng t&ocirc;i hng n l&agrave;m vic vi t&agrave;i x qua c&aacute;c bui hp n&ecirc;u &yacute; kin, c&aacute;c bui &agrave;o to, c&aacute;c lp k nng v&agrave; kim tra cht lng,&hellip;Kt qu t c&aacute;c chng tr&igrave;nh n&agrave;y t&ugrave;y thuc v&agrave;o kh nng tip thu ca c&aacute;c t&agrave;i x.</li>
                    <li>T&agrave;i x bt buc tham gia c&aacute;c bui &agrave;o to c t chc bi 8Go hoc c&aacute;c i t&aacute;c ca 8Go, tr trng hp khn cp hoc l&yacute; do bt kh kh&aacute;ng. Trong nhng trng hp &oacute;, 8Go s sp xp c&aacute;c bui thay th v&agrave;o mt ng&agrave;y kh&aacute;c.</li>
                </ol>
                <h4><strong>3. TH TC B&Aacute;O C&Aacute;O T&Igrave;NH HUNG KHN CP</strong></h4>
                <h4><strong>4. CI THIN V&Agrave; KH&Ocirc;NG NGNG TRAU DI BN TH&Acirc;N</strong></h4>
                <p>Tt c t&agrave;i x phi x&aacute;c nhn rng h hiu r&otilde; tt c c&aacute;c iu khon trong b quy tc ng x ca t&agrave;i x n&ecirc;u tr&ecirc;n v&agrave; phi chu tr&aacute;ch nhim cho bt k vic l&agrave;m l&agrave; kt qu hoc c x&aacute;c nhn l&agrave; kt qu ca qu&aacute; tr&igrave;nh hot ng dch v di chuyn bng xe 4 b&aacute;nh, bao gm v&agrave; kh&ocirc;ng ch c&oacute; thit hi v t&iacute;nh mng v&agrave; t&agrave;i sn hay chn thng c&aacute; nh&acirc;n. T&agrave;i x cng s b qui tr&aacute;ch nhim i vi c&aacute;c thit hi 8Go phi chu nu b ph&aacute;t hin vi phm bt k iu khon n&agrave;o trong b quy tc ng x.</p>
                <p>Hn na, t&agrave;i x phi hiu v&agrave; ng &yacute; rng 8Go c&oacute; quyn x l&yacute; nhng h&agrave;nh vi vi phm, bao gm vic chn tm thi hoc vnh vin t&agrave;i x khi ng dng 8Go nu t&agrave;i x vi phm bt k iu khon n&agrave;o ca b quy tc ng x n&agrave;y.</p>
            </div>
        </div>
    </div>
</template>

