<template>
  <el-autocomplete
    v-model="autocompleteText"
    :clearable=true
    :fetch-suggestions="onAddressSuggest"
    placeholder="Nhp a ch"
    @blur="onAddressBlur"
    @focus="onAddressFocus"
    @select="onAddressSelected">
    <i class="el-icon-location el-input__icon" slot="prefix"
       @click="onLocationIconClick"></i>
    <template class="custom-item" slot-scope="{ item }" style="border-radius: 0; padding: 0">
      <div class="complete-item">
        <div style="float: left" class="auto-complete-name">
          <b>{{item['structured_formatting']['main_text']}}</b>
        </div>
        <!--<div style="float: right">-->
          <!--<el-tag size="mini" style="margin-right: 0;">{{ address_type[item['types'][0]]}}</el-tag>-->
        <!--</div>-->
        <p class="auto-complete-address" style="font-size: 1.2rem; clear: both; margin-top: -10px">
          <span class="link">{{item['structured_formatting']['secondary_text']}}</span>
        </p>
      </div>
    </template>
  </el-autocomplete>
</template>

<script>
  import Vue from 'vue'
  import axios from 'axios'
  import {uuid} from 'vue-uuid'
  import geolocation from 'vue-browser-geolocation';

  Vue.use(geolocation);

  const MAPS_AUTOCOMPLETE_API_URL = 'https://maps.googleapis.com/maps/api/place/autocomplete/json';
  const MAPS_GEOCODE_API_URL = 'https://maps.googleapis.com/maps/api/geocode/json';
  const MAPS_AUTOCOMPLETE_API_KEY = 'AIzaSyBZOT_-R6sqnAR4kmbRLx-LHCX2Hbm5GgA';
  const COMPONENTS = 'country:vn';

  const ADDRESS_COMPONENTS = {
    subpremise: 'short_name',
    street_number: 'short_name',
    route: 'long_name',
    locality: 'long_name',
    administrative_area_level_1: 'short_name',
    administrative_area_level_2: 'long_name',
    country: 'long_name',
    postal_code: 'short_name'
  };

  const CITIES_TYPE = [
    'locality',
    'administrative_area_level_3'
  ];

  const REGIONS_TYPE = [
    'locality',
    'sublocality',
    'postal_code',
    'country',
    'administrative_area_level_1',
    'administrative_area_level_2'
  ];

  const ADDRESS_TYPE = {
    'transit_station': 'Trm trung chuyn',
    'establishment': 'Doanh nghip',
    'street_address': 'ng',
    'premise': 'Ta nh',
    'neighborhood': 'Khu  th',
    'route': 'ng',
    'locality': 'a phng',
    'administrative_area_level_1': 'Tnh/Thnh ph',
    'administrative_area_level_2': 'Qun/Huyn',
    'sublocality_level_1': 'Phng/X',
    'natural_feature': 'Sng nc'
  };

  export default {
    name: 'AddressAutoComplete',
    props: {
      classname: String,

      id: {
        type: String,
        required: true
      },

      placeholder: {
        type: String,
        default: ''
      },

      types: {
        type: String,
        default: 'address'
      },

      country: {
        type: [String, Array],
        default: null
      },

      enableGeolocation: {
        type: Boolean,
        default: false
      },

      geolocationOptions: {
        type: Object,
        default: null
      }
    },

    data() {
      return {
        /**
         * Address type in Vietnamese
         * @type {String}
         */
        address_type: ADDRESS_TYPE,

        /**
         * Session token
         * @type {String}
         */
        sessionToken: '',

        /**
         *  Airport list
         *  @type {Array}
         */
        airports: null,

        /**
         *  Latitude
         *  @type {Number}
         */
        latitude: 0,

        /**
         *  Longitude
         *  @type {Number}
         */
        longitude: 0,

        /**
         * Autocomplete input text
         * @type {String}
         */
        autocompleteText: ''
      }
    },

    mounted: function () {
      this.sessionToken = uuid.v1();
      this.airports = this.getPreAirportList();
    },

    methods: {

      onAddressFocus() {
        // console.log('onAddressFocus')
      },

      onAddressBlur() {
        // console.log('onAddressBlur')
      },
      /**
       *
       */
      onAddressSuggest(keyword, cb) {
        if (keyword.length > 3) {
          let params = {
            input: keyword,
            components: COMPONENTS,
            key: MAPS_AUTOCOMPLETE_API_KEY,
            sessiontoken: this.sessionToken,
          };

          // type == airport -> load all saa
          axios.get(MAPS_AUTOCOMPLETE_API_URL, {params: params})
            .then(function (response) {
              if (response.status === 200 && response.data['status'] === 'OK') {
                let predictions = response.data['predictions'];
                let complete_address = {};

                predictions.forEach((prediction) => {
                  console.log(prediction['description']);
                  // console.log(prediction['description']);
                  // console.log("index: " + index)
                  // console.log(prediction['id']);
                  // console.log(prediction['place_id']);
                  console.log(prediction['types'][0]);
                  // console.log(prediction['structured_formatting']['main_text']);
                  // console.log(prediction['structured_formatting']['secondary_text']);

                  let id = prediction['id'];
                  let place_id = prediction['place_id'];
                  let type = prediction['types'][0];
                  let main_text = prediction['structured_formatting']['main_text'];
                  let secondary_text = prediction['structured_formatting']['secondary_text'];

                  complete_address['id'] = id;
                  complete_address['place_id'] = place_id;
                  complete_address['type'] = type;
                  complete_address['name'] = main_text;
                  complete_address['address'] = secondary_text;
                });
                cb(predictions);
              }
            })
            .catch(function (error) {
              // console.log(error);
            })
            .then(function () {
              // console.log('axios.then')
            });
        } else {
          if (this.types === 'departure') {
            let airports = keyword ? this.airports.filter(this.getAirportFilter(keyword)) : this.airports;
            cb(airports)
          }
        }
      },

      /**
       *
       */
      onAddressSelected(item) {
        console.log(item['id']);
        console.log(item['place_id']);
        console.log(item['types'][0]);
        console.log(item.structured_formatting.main_text);
        console.log(item.structured_formatting.secondary_text);

        this.autocompleteText = item.structured_formatting.main_text;
      },

      /**
       * Location icon click
       */
      onLocationIconClick(event) {
        this.$getLocation({
          enableHighAccuracy: true,
          timeout: Infinity,
          maximumAge: 0
        }).then(coordinate => {
          let self = this;

          this.latitude = coordinate['lat'];
          this.longitude = coordinate['lng'];

          if (this.latitude !== undefined && this.longitude !== undefined) {
            self.autocompleteText = "ang ly v tr hin ti...";

            let params = {
              latlng: this.latitude + ',' + this.longitude,
              key: MAPS_AUTOCOMPLETE_API_KEY
            };

            axios.get(MAPS_GEOCODE_API_URL, {params: params})
              .then(function (response) {
                if (response.status === 200 && response.data['status'] === 'OK') {
                  if (response.data['results'].length > 0) {
                    self.autocompleteText = response.data['results'][0]['formatted_address']
                  }
                }
              })
              .catch(function (error) {
                // console.log(error);
              })
              .then(function () {
              });
          } else {
            self.autocompleteText = "Khng tm c v tr";
          }
        });
      },

      /**
       * Create airport filter by keyword
       * @return
       */
      getAirportFilter(keyword) {
        return (airport) => {
          return (airport.iata_code.toLocaleLowerCase().includes(keyword.toLowerCase())) || (airport.structured_formatting.main_text.toLowerCase().includes(keyword.toLowerCase()))
        }
      },

      /**
       * Get predefined airport list
       * @returns {*[]}
       */
      getPreAirportList() {
        return [
          {
            'id': 'eda2e01466af32bdb66abe203bbfe32d68233185',
            'place_id': 'ChIJU8SvORUCNTERtCYCUqP64OY',
            'description': 'Sn Bay Quc T Ni Bi (HAN)',
            'iata_code' : 'HAN',
            'types': ['establishment'],
            'structured_formatting': {
              'main_text': 'Sn Bay Quc T Ni Bi (HAN)',
              'secondary_text': 'H Ni, Vit Nam'
            }
          },
          {
            'id': '82615400088fea1f9a3150a30455d5fbe591d6ea',
            'place_id': 'ChIJnZ-oGhEpdTER8ycbqsCc8Ng',
            'description': 'Sn bay quc t Tn Sn Nht (SGN)',
            'iata_code' : 'SGN',
            'types': ['establishment'],
            'structured_formatting': {
              'main_text': 'Sn Bay Quc T Tn Sn Nht (SGN)',
              'secondary_text': 'H Ch Minh, Vit Nam'
            }
          },
          {
            'id': 'd5d3646042ce1e19e6efdbe80d91b096b3eb5e17',
            'place_id': 'ChIJ0VLfc6IZQjERGgeVovz5A7I',
            'description': 'Sn Bay Quc t  Nng (DAD)',
            'iata_code' : 'DAD',
            'types': ['establishment'],
            'structured_formatting': {
              'main_text': 'Sn bay Quc t  Nng (DAD)',
              'secondary_text': 'Hi Chu,  Nng, Vit Nam'
            }
          },
          {
            'id': 'd5d3646042ce1e19e6efdbe80d91b096b3eb5e17',
            'place_id': 'ChIJ0VLfc6IZQjERGgeVovz5A7I',
            'description': 'Sn bay Quc T Cam Ranh (CRX)',
            'iata_code' : 'CRX',
            'types': ['establishment'],
            'structured_formatting': {
              'main_text': 'Sn bay Quc T Cam Ranh (CRX)',
              'secondary_text': 'Khnh Ha, Vit Nam'
            }
          },
          {
            'id': '3206633231f33e86f12892ce25ce5a3633413830',
            'place_id': 'ChIJ6Zt2QAJwSjERwib7yaP2-Nc',
            'description': 'Sn Bay Ct Bi (HPH)',
            'iata_code' : 'HPH',
            'types': ['establishment'],
            'structured_formatting': {
              'main_text': 'Sn Bay Ct Bi (HPH)',
              'secondary_text': 'Hi Phng, Vit Nam'
            }
          },
          {
            'id': 'b020a355a3ea6087934e78cf56e9042d0725c626',
            'place_id': 'ChIJk6RzPIeLpzERGN0aOK9WNEE',
            'description': 'Sn Bay Ph Quc (PQC)',
            'iata_code' : 'PQC',
            'types': ['establishment'],
            'structured_formatting': {
              "main_text": "Sn Bay Ph Quc (PQC)",
              "secondary_text": "Kin Giang, Vit Nam"
            }
          }
        ]
      },
    }
  }
</script>

<style scoped>
  @import "../../assets/css/booking/address_auto_complete.css";
</style>



